
import CommonHeader from '@/pages/common/header.vue'
import {SIGN_UP} from '@/api/activitySignUp'
import Alert from 'vux/src/components/alert/index.vue'

export default {
name: 'Detail',
data () {
return {
show: false,
message: '',
hasSign: true
}
},
computed: {
activity () {
return this.$store.getters.getTodoById(this.$route.params.id)
}
},
components: {
CommonHeader,
Alert
},
methods: {
handleSignUp () {
SIGN_UP({
activity: this.activity
}).then(res => {
console.log(res)
this.message = res.msg
this.show = true
})
},
onHide () {
this.hasSign = false
}
},
created () {
this.$store.dispatch('getActivities')
}
}



